import debug from 'debug'

const BASE = 'use-interaction'
const COLOURS = {
  trace: '#1585c7',
  info: '#61dafb',
  warn: '#ff9e2c',
  error: '#c71585',
} // choose better colours :)

class Log {
  generateMessage(level, args) {
    // Set the prefix which will cause debug to enable the message
    const namespace = `${BASE}:${level}`
    const createDebug = debug(namespace)

    // Set the colour of the message based on the level
    createDebug.color = COLOURS[level]

    if (args.length > 1) {
      createDebug(args[0], ...args)
    } else {
      createDebug(args)
    }
  }

  trace(...args) {
    return this.generateMessage('trace', args)
  }

  info(...args) {
    return this.generateMessage('info', args)
  }

  warn(...args) {
    return this.generateMessage('warn', args)
  }

  error(...args) {
    return this.generateMessage('error', args)
  }
}

export default new Log()
